:root {
  --blue-primary: #3a7bfd;
  --blue-secondary: #083693;
  --orange-primary: #ff6f00;
  --orange-secondary: #db6205;
  --color-text-primary: #9394a5;
  --color-text-secondary: #393a4c;
  --color-text-tertiary: #f5f5f5;
  --font: 'Josefin Sans', sans-serif; 


  /* Light */
  --light-primary: #fafafa;
  --light-secondary: #e4e5f1;

  /* Dark */

  --dark-primary: #25273c;
  --dark-secondary: #161722;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: border-box;
}
body {
  font-size: 1.8rem;
  line-height: 1.5;
  font-family: var(--font);
  margin: 0;
}
h1, h2, h3 {
  margin: 0 0 2rem 0;
  line-height: 1.5;
}
h1, h2 {
  font-weight: 700;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}

p {
  margin: 0 ;
}

img {
  max-width: 100%;
}
.container-app {
  display: flex;
  min-height: 100vh;
}
.aside {
  flex: 0 0 30rem;
}
.main-section {
  padding: 2rem;
  flex: 1;
}
@media (max-width: 745px) { 
  .container-app {
    display: block;
  }
  .aside {
    flex: 0 0 30rem;
  }
  .main-section {
    flex: 1;
  }
}
